@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

:root {
  --primary-color-1-1: #1c1c1f;
  --primary-color-1-2: #1a1a1d;
  --primary-color-1-3: #17171a;
  --primary-color-1-4: #151517;
  --primary-color-1-5: #121214;
  --primary-color-1-6: #0f0f12;

  --primary-color-2-1: #efefef;
  --primary-color-2-2: #d9d9d9;
  --primary-color-2-2: #f9fafb;

  --primary-color-3-1: #ad0537;
  --primary-color-3-2: #990531;
  --primary-color-3-3: #8a042c;
  --primary-color-3-4: #400315;

  --black: #030303;
}

body {
  cursor: none;
  font-family: "Poppins";
}

button:hover {
  cursor: none;
}

.custom-cursor {
  width: 0.4rem;
  height: 0.4rem;
  position: fixed;
  top: 3px;

  background: var(--primary-color-3-1);
  border-radius: 100%;

  pointer-events: none;
  z-index: 9999999;

  transition: all 50ms ease, width 300ms ease, height 300ms ease;

  &::after {
    content: "";
    opacity: 0.7;

    width: 5rem;
    height: 5rem;
    border: 2px solid var(--primary-color-3-3);
    background-color: rgba(255, 255, 255, 0);
    border-radius: 50%;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    transition: background-color 300ms ease, transform 300ms ease;
  }
}

.custom-cursor.hovering {
  &::after {
    transform: translate(-50%, -50%) scale(0.8);
    background-color: rgba(138, 4, 44, 0.25);
    transition: background-color 300ms ease, transform 300ms ease;
  }
}
